
.b8t_brand {
    white-space: nowrap;
}

.b8t_brand a {
    font-variant: small-caps;
    font-size: 1.5em;
    line-height: 1.5em;
}

.b8t_brand span {
    color: red !important;
    font-size: 0.75em;
    font-weight: bold;
    font-style: italic;
}

.b8t_brand sup {
    font-size: 0.5em;
}

.b8t_nav {
    line-height: 6em;
}

.slogan {
    font-style: italic;
    font-size: 0.6em;
    line-height: 0.6em;
    padding: 0 0 0.1em 0;
}

p { text-align: justify;}

mark {
    font-weight: bold; color: navy;
}

mark::before, mark::after {
    content: " ";
}

del {
    background-color: lightgray;
}

.current {
  color: red !important;
}

.b8t_clr {
    clear: both;
}

.pagination a, .pagination span {
    padding: 7px 18px;
    border: 1px solid #eee;
    margin-left: -2px;
    margin-right: -2px;
    background-color: #ffffff;
    display: inline-block;
  }

.pagination a {    
    &:hover {
        background-color: #f1f1f1;
        color: #333;
    }
 }

.pagination {
    text-align: center;
 }


